// src/pages/LoginPage.jsx
import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../contexts/AuthContext";

function LoginPage() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  //    
  const [isAdmin, setIsAdmin] = useState(false);

  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const navigate = useNavigate();
  const { login } = useContext(AuthContext);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    if (!username || !password) {
      setError(",   .");
      setIsLoading(false);
      return;
    }

    //   ""  ,
    // ..        /api/login.
    //   ,  token + role  .
    try {
      //     ,   :
      // const response = await fetch("https://inotex-analytics.onrender.com/api/login", ...)
      // if (response.ok) { ... } else { ... }

      //   ,   "demo"/"demo"  :
      if (username === "demo" && password === "demo") {
        const fakeToken = "fake_jwt_token";
        const role = isAdmin ? "admin" : "owner";
        //   
        login(fakeToken, role);
        //     
        if (role === "admin") {
          navigate("/admin");
        } else {
          navigate("/owner");
        }
      } else {
        //  
        setError("    .");
      }
    } catch (error) {
      console.error("  :", error);
      setError(" .  .");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="flex items-center justify-center h-screen bg-gray-100">
      <div className="bg-white p-6 rounded shadow-md w-full max-w-sm">
        <h1 className="text-2xl font-bold text-center mb-4"></h1>
        {error && (
          <div className="bg-red-100 text-red-700 p-2 mb-4 rounded">
            {error}
          </div>
        )}
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700">
               
            </label>
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full p-2 border border-gray-300 rounded mt-1"
              placeholder="  "
            />
          </div>
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700">
              
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full p-2 border border-gray-300 rounded mt-1"
              placeholder=" "
            />
          </div>

          {/*  "?" */}
          <div className="mb-4 flex items-center">
            <input
              id="adminCheck"
              type="checkbox"
              checked={isAdmin}
              onChange={() => setIsAdmin(!isAdmin)}
              className="mr-2"
            />
            <label htmlFor="adminCheck" className="text-sm text-gray-700">
              
            </label>
          </div>

          <button
            type="submit"
            className={`w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600 transition ${
              isLoading ? "cursor-not-allowed opacity-50" : ""
            }`}
            disabled={isLoading}
          >
            {isLoading ? "..." : ""}
          </button>
        </form>
      </div>
    </div>
  );
}

export default LoginPage;
