// src/pages/OwnerAnalyticsPage.jsx
import React, { useEffect, useState } from "react";
import { Bar, Doughnut } from "react-chartjs-2";
import "chart.js/auto";

/**
 *      ,
 *    .
 */
function formatNumber(value) {
  if (!value) return value;
  const num = Number(value.toString().replace(/\s+/g, ""));
  if (Number.isNaN(num)) return value;
  return num.toLocaleString("ru-RU").replace(/,/g, " ");
}

function createDoughnutData(valueStr) {
  if (!valueStr || typeof valueStr !== "string") {
    return {
      labels: ["%  ", " 100%"],
      datasets: [{ data: [0, 100], backgroundColor: ["#4CAF50", "#FF7043"] }],
    };
  }
  const numeric = parseFloat(valueStr.replace("%", "")) || 0;
  return {
    labels: ["%  ", " 100%"],
    datasets: [
      {
        data: [numeric, 100 - numeric],
        backgroundColor: ["#4CAF50", "#FF7043"],
        hoverBackgroundColor: ["#66BB6A", "#FF8A65"],
      },
    ],
  };
}

function createBarData(labels, arr1, arr2, label1, label2) {
  const safeLabels = Array.isArray(labels) ? labels : [];
  const safeArr1 = Array.isArray(arr1)
    ? arr1.map((v) => parseFloat(v) || 0)
    : [];
  const safeArr2 = Array.isArray(arr2)
    ? arr2.map((v) => parseFloat(v) || 0)
    : [];

  return {
    labels: safeLabels,
    datasets: [
      {
        label: label1,
        data: safeArr1,
        backgroundColor: "#2196F3",
      },
      {
        label: label2,
        data: safeArr2,
        backgroundColor: "#4CAF50",
      },
    ],
  };
}

function OwnerAnalyticsPage() {
  const [data, setData] = useState(null);

  //      localStorage "analyticsData"
  useEffect(() => {
    const stored = localStorage.getItem("analyticsData");
    if (stored) {
      setData(JSON.parse(stored));
    }
  }, []);

  if (!data) {
    return (
      <div className="text-center mt-8">
        <p className="text-gray-600">    .</p>
      </div>
    );
  }

  const { sbyt, budget, remont } = data;
  const skladValue = formatNumber(sbyt.sklad);
  const leftoverValue = formatNumber(budget.remaining);

  return (
    <div className="container mx-auto p-4">
      <div className="w-full flex flex-col gap-4">
        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-2">
          <div className="bg-white rounded-md shadow-md p-3 text-center text-xs sm:text-sm md:text-base">
            <p className="text-gray-600"> %  </p>
            <p className="font-bold mt-1 text-lg sm:text-xl md:text-2xl">
              {sbyt.factPercentPlan ?? ""}
            </p>
          </div>
          <div className="bg-white rounded-md shadow-md p-3 text-center text-xs sm:text-sm md:text-base">
            <p className="text-gray-600">  </p>
            <p className="font-bold mt-1 text-lg sm:text-xl md:text-2xl">
              {skladValue ?? ""}
            </p>
          </div>
          <div className="bg-white rounded-md shadow-md p-3 text-center text-xs sm:text-sm md:text-base">
            <p className="text-gray-600">  </p>
            <p className="font-bold mt-1 text-lg sm:text-xl md:text-2xl">
              {budget.percent ?? ""}
            </p>
          </div>
          <div className="bg-white rounded-md shadow-md p-3 text-center text-xs sm:text-sm md:text-base">
            <p className="text-gray-600">   </p>
            <p className="font-bold mt-1 text-lg sm:text-xl md:text-2xl">
              {leftoverValue ?? ""}
            </p>
          </div>
          <div className="bg-white rounded-md shadow-md p-3 text-center text-xs sm:text-sm md:text-base">
            <p className="text-gray-600"></p>
            <p className="font-bold mt-1 text-lg sm:text-xl md:text-2xl">
              {sbyt.date ?? ""}
            </p>
          </div>
        </div>

        <div className="grid grid-cols-1 xl:grid-cols-2 gap-4">
          <div className="flex flex-col gap-4">
            <div className="bg-white rounded-md shadow-md p-4 flex flex-col items-center">
              <p className="text-lg font-bold mb-2">
                %    
              </p>
              <div className="w-64 h-64">
                <Doughnut data={createDoughnutData(sbyt.onePhasePercent)} />
              </div>
            </div>

            <div className="bg-white rounded-md shadow-md p-4 flex flex-col items-center">
              <p className="text-lg font-bold mb-2">
                %    
              </p>
              <div className="w-64 h-64">
                <Doughnut data={createDoughnutData(sbyt.threePhasePercent)} />
              </div>
            </div>
          </div>

          <div className="grid grid-rows-2 gap-4">
            <div className="bg-white rounded-md shadow-md p-4">
              <p className="text-lg font-bold mb-2"> </p>
              <div className="h-60">
                <Bar
                  data={createBarData(
                    budget.dates,
                    budget.plan,
                    budget.fact,
                    "",
                    ""
                  )}
                  options={{
                    responsive: true,
                    maintainAspectRatio: false,
                    scales: {
                      y: { beginAtZero: true },
                    },
                  }}
                />
              </div>
            </div>

            <div className="bg-white rounded-md shadow-md p-4">
              <p className="text-lg font-bold mb-2"></p>
              <div className="h-60">
                <Bar
                  data={createBarData(
                    remont.dates,
                    remont.inRepair,
                    remont.repaired,
                    "  ",
                    ""
                  )}
                  options={{
                    responsive: true,
                    maintainAspectRatio: false,
                    scales: {
                      y: { beginAtZero: true },
                    },
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OwnerAnalyticsPage;
